.navbar {
    position: fixed;
    top: 0;
    background-color: transparent;
    width: 100%;
    height: 100px;
    padding-left: 20px;
    font-size: 20px;
    z-index: 1;
}
.scrolling{
    background-color: #1b1d23;
    transition: all 0.5s ease;
}
.navbar-logo {
    height: 40px;
    width: 40px;
    margin-left: 10px;
    /* line-height: 80px; */
    color: white;
    cursor: pointer;
}
.navbar-logo > img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}
